@import "shared/styles/helpers.scss";

.appbar {
	position: fixed;
	:global {
		.MuiToolbar-root {
			padding: 8px 24px;
			.logo {
				display: flex;
				align-items: center;
				justify-content: center;
				img {
					width: 3vw;
					min-width: 30px;
					margin-right: 10px;
				}
				@include sm-screen {
					display: none;
				}
			}

			.searchBox {
				width: 50%;
				.MuiTextField-root {
					border-radius: 25px;
					.MuiOutlinedInput-notchedOutline {
						border: none;
					}
				}
			}
		}
	}
}

.MuiMenu_custom {
	:global {
		.MuiPaper-root {
			border-radius: 10px;
			.MuiList-root {
				padding: 8px;
				.MuiMenuItem-root {
					border-radius: 10px;
				}
			}
		}
	}
}
