/************ VARIABLES ************/
$primary_color: #565df5;
$secondary_color: #c53889;
$google_color_main: #ea4335;
$google_color_dark: #c0372a;
$primary_gradiant: linear-gradient(90deg, $primary_color, #34d4db);
$secondary_gradiant: linear-gradient(90deg, $secondary_color, #f748e8);

/************ MIXINS ************/

@mixin lg-screen {
	@media (max-width: 1200px) {
		@content;
	}
}

@mixin md-screen {
	@media (max-width: 900px) {
		@content;
	}
}

@mixin sm-screen {
	@media (max-width: 600px) {
		@content;
	}
} ;
