@import "shared/styles/helpers";

@mixin overlayPosition($dir) {
	#{$dir}: 0;
	clip-path: ellipse(100% 120% at $dir);
}

.authLayout {
	height: 100vh;
}

.authBox {
	width: 100%;
	height: 100%;
	overflow-y: scroll;
	overflow-x: hidden;
	background-image: linear-gradient(0deg, #c53888b5, transparent);
}

.overlay {
	position: absolute;
	width: 50%;
	height: 100%;
	&.overlayLeft {
		@include overlayPosition(left);
	}
	&.overlayRight {
		@include overlayPosition(right);
	}
	&.animateLeft {
		animation: slideLeft 0.5s ease;
		@include sm-screen {
			animation: none;
		}
	}
	&.animateRight {
		animation: slideRight 0.5s ease;
		@include sm-screen {
			animation: none;
		}
	}
	@include md-screen {
		width: 65%;
	}
	@include sm-screen {
		width: 100%;
		clip-path: none !important;
	}
}

@keyframes slideLeft {
	0% {
		left: 50%;
		clip-path: ellipse(100% 120% at right);
	}
	70% {
		clip-path: ellipse(100% 120% at left);
	}
	100% {
		left: 0;
	}
}

@keyframes slideRight {
	0% {
		right: 50%;
		clip-path: ellipse(100% 120% at left);
	}
	70% {
		clip-path: ellipse(100% 120% at right);
	}
	100% {
		right: 0;
	}
}
