@import "shared/styles/helpers.scss";

.chatModal {
	top: initial !important;
	left: initial !important;
	right: 10% !important;
	width: 350px;
	height: 70%;
	display: flex;
	flex-direction: column;
	background-color: white;
	&.minimized {
		height: auto;
	}
	@include sm-screen {
		right: 0% !important;
		width: 250px;
	}
	:global {
		.header {
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding: 10px;
			background-color: $primary-color;
			color: white;
		}
	}
}

.messages {
	flex: 1;
	padding: 10px 10px 20px;
	overflow: auto;
	display: flex;
	flex-direction: column-reverse;
	:global {
		.message {
			margin-bottom: 5px;
			display: flex;
			justify-content: flex-end;
			text-align: end;
			.content {
				padding: 7px 10px;
				border-radius: 10px;
				color: white;
				background-color: $primary-color;
			}
			&.buddy {
				justify-content: flex-start;
				text-align: start;
				.content {
					background-color: $secondary-color;
				}
			}
		}
	}
}

.messageInput {
	padding: 10px;
	display: flex;
	align-items: center;
	padding-top: 10px;
	:global {
		.MuiTextField-root {
			flex: 1;
			border-radius: 25px;
			.MuiInputBase-root {
				padding: 5px 15px;
			}
			.MuiOutlinedInput-notchedOutline {
				border: none;
			}
		}
	}
}
