@import "shared/styles/helpers";

.loginContainer {
	position: relative;
	z-index: 100;
	display: flex;
	flex-direction: column;
	height: 100%;
	width: 70%;
	margin: auto;
	padding: 20px 0;
}

.body {
	flex: 1;
	display: flex;
	align-items: center;
}

.googleAuth {
	background-color: $google_color_main !important;
	&:hover {
		background-color: $google_color_dark !important;
	}
}
