.profileImg {
	position: relative;
	width: 30vw;
	height: 30vw;
	max-width: 150px;
	max-height: 150px;
	margin: auto;
}

.imgThumb {
	width: 100%;
	height: 100%;
	:global {
		.imageInput {
			display: none;
		}
		img {
			width: 100%;
			height: 100%;
			border-radius: 50%;
			object-fit: cover;
		}
	}
}

.toggleButton {
	position: absolute !important;
	bottom: 0;
	right: 0;
}
.imageInputLabel {
	display: flex;
	width: 100%;
	cursor: pointer;
}

.imgModal {
	display: flex;
	justify-content: center;
	align-items: center;
	:global {
		.imgContainer {
			width: 80%;
			height: 80%;
			display: flex;
			justify-content: center;
			align-items: center;
			.closeButton {
				position: absolute;
				left: 25px;
				top: 25px;
			}
			.img {
				display: block;
				max-width: 100%;
				max-height: 100%;
			}
		}
	}
}

.MuiMenu_custom {
	:global {
		.MuiPaper-root {
			border-radius: 10px;
			.MuiList-root {
				padding: 8px;
				.MuiMenuItem-root {
					border-radius: 10px;
				}
			}
		}
	}
}
