@import "shared/styles/helpers.scss";

.utilsBar {
	position: fixed !important;
	z-index: 1000;
	transition: all 0.3s ease;
	overflow: hidden;
	:global {
		.toggleButton {
			display: flex;
			justify-content: flex-end;
			.MuiIconButton-root {
				margin: 0 8px;
			}
		}

		.MuiListItemButton-root {
			.MuiListItemIcon-root {
				min-width: 35px;
			}
			.MuiListItemText-root {
				margin: 0;
				white-space: nowrap;
				.MuiTypography-root {
					font-weight: bold;
				}
			}
		}

		.activeLink .MuiListItemButton-root {
			border-right: 3px solid $secondary_color;
		}
	}
}
