@import "shared/styles/helpers.scss";

.newPostModal {
	display: flex;
	align-items: center;
	justify-content: center;
	:global {
		.newPostInput {
			width: 50%;
			max-height: 80%;
			overflow: scroll;
			border-radius: 10px;
			@include sm-screen {
				width: 90%;
			}
			.header {
				display: flex;
				align-items: center;
				padding: 8px;
				.MuiTypography-root {
					flex: 1;
					text-align: center;
				}
			}
			.body {
				padding: 20px;
				.MuiInputBase-root::before,
				.MuiInputBase-root::after {
					display: none;
				}
				.imageViewer {
					position: relative;
					padding: 10px;
					margin: 10px 0;
					border-radius: 10px;
					.deleteButton {
						position: absolute;
						left: 15px;
						top: 15px;
						z-index: 100;
					}
					.MuiImageList-root {
						margin: 0;
					}
				}
				.addons {
					padding: 5px 10px;
					display: flex;
					align-items: center;
					border-radius: 10px;
					.MuiTypography-root {
						flex: 1;
					}
				}
			}
		}
	}
}

.newPostTab {
	margin-bottom: 25px;
	padding: 18px;
	:global {
		.elementsContainer {
			display: flex;
			align-items: center;
			.MuiAvatar-root {
				margin-right: 16px;
			}
			.createButton {
				padding: 10px 20px;
				flex: 1;
				border-radius: 30px;
				cursor: pointer;
			}
		}
	}
}
