@import "./helpers";
@import "./fonts";

* {
	box-sizing: border-box;
	scroll-behavior: smooth;
}

html {
	font-size: 20px;
	@include lg-screen {
		font-size: 16px;
	}
	@include md-screen {
		font-size: 14px;
	}
	@include sm-screen {
		font-size: 12px;
	}
}

body {
	margin: 0;
}

a {
	text-decoration: none;
	color: inherit;
}

::-webkit-scrollbar {
	width: 5px;
	height: 3px;
}

::-webkit-scrollbar-track {
	background: transparent;
}

::-webkit-scrollbar-thumb {
	background-color: $primary_color;
	border-radius: 20px;
}

::-ms-reveal {
	display: none;
}
