@import "shared/styles/helpers";

.stepperContainer {
	position: fixed;
	z-index: 100;
	width: 100%;
	:global {
		.MuiStepConnector-root {
			top: 22px;
			left: calc(-50% + 23px);
			right: calc(50% + 23px);
			@include lg-screen {
				top: 19px;
				left: calc(-50% + 20px);
				right: calc(50% + 20px);
			}
			@include md-screen {
				top: 18px;
				left: calc(-50% + 19px);
				right: calc(50% + 19px);
			}
			@include sm-screen {
				top: 16px;
				left: calc(-50% + 18px);
				right: calc(50% + 18px);
			}
			.MuiStepConnector-line {
				border-width: 0;
				height: 3px;
				width: 0;
				background-image: $secondary_gradiant;
				:local {
					animation: shrinkX 0.3s ease;
				}
			}
			&.Mui-active,
			&.Mui-completed {
				.MuiStepConnector-line {
					width: 100%;
					:local {
						animation: stretchX 0.4s ease;
					}
				}
			}
		}
		.MuiStepLabel-iconContainer {
			padding: 8px !important;
			border-radius: 50%;
			.MuiSvgIcon-root {
				fill: white;
			}
			&.Mui-active,
			&.Mui-completed {
				background-image: $secondary_gradiant;
				box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.25);
			}
		}
	}
}

.megaForm {
	:global {
		.MuiTableCell-head {
			font-weight: 700;
		}
	}
}

.review {
	:global {
		.profileImg {
			width: 30vw;
			height: 30vw;
			max-width: 150px;
			max-height: 150px;
			border-radius: 50%;
			object-fit: cover;
		}
	}
}

.controls {
	display: flex;
	justify-content: space-between;
}

@keyframes stretchX {
	0% {
		width: 0;
	}
	100% {
		width: 100%;
	}
}

@keyframes shrinkX {
	0% {
		width: 100%;
	}
	100% {
		width: 0%;
	}
}
