.imagesViewer {
	:global {
		.MuiImageListItem-root {
			cursor: pointer;
			img {
				max-height: 500px;
			}
			.overlay {
				position: absolute;
				width: 100%;
				height: 100%;
				display: flex;
				align-items: center;
				justify-content: center;
				background-color: rgba(0, 0, 0, 0.6);
			}
		}
	}
}

.actions {
	width: 100%;
	display: flex;
	width: 100%;
	:global {
		.MuiButton-root {
			flex: 1;
			font-weight: bold;
		}
	}
}

.commentsList {
	padding: 0 10px;
	max-height: 300px;
	overflow-y: scroll;
	:global {
		.comment {
			display: flex;
			margin: 15px 0;
			.MuiAvatar-root {
				margin-right: 10px;
			}
			.commentBox {
				padding: 10px;
				border-radius: 20px;
			}
		}
	}
}

.commentField {
	padding: 10px;
	display: flex;
	align-items: center;
	padding-top: 10px;
	:global {
		.MuiTextField-root {
			flex: 1;
			border-radius: 25px;
			.MuiInputBase-root {
				padding: 5px 15px;
			}
			.MuiOutlinedInput-notchedOutline {
				border: none;
			}
		}
	}
}

.interactions {
	display: flex;
	justify-content: space-between;
	padding: 0 20px 10px;
	:global {
		.actionCounter {
			display: flex;
			align-items: center;
			.MuiTypography-root {
				margin-right: 5px;
			}
		}
		.actionCounterGroup {
			flex: 1;
			display: flex;
			justify-content: flex-end;
		}
	}
}

.imageModal {
	display: flex;
	justify-content: space-around;
	align-items: center;
	height: 100%;
	:global {
		img {
			max-width: 80%;
			object-fit: cover;
		}
		.closeButton {
			position: absolute;
			top: 10px;
			left: 10px;
		}
		.nextButton {
			position: absolute;
			right: 10px;
		}
		.prevButton {
			position: absolute;
			left: 10px;
		}
	}
}

.MuiMenu_custom {
	:global {
		.MuiPaper-root {
			border-radius: 10px;
			.MuiList-root {
				padding: 8px;
				.MuiMenuItem-root {
					border-radius: 10px;
				}
			}
		}
	}
}